$color-1: #1E1E1E;
$color-2: #F9F9F9;

$main-color:  #FDAB72;

@font-face {
    font-family: "font-1";
    src: url("../assets/fonts/AbhayaLibre-Regular.ttf");
}

@font-face {
    font-family: "font-2";
    src: url("../assets/fonts/ABeeZee-Regular.ttf");
}

$font-1: "font-1" , sans-serif;
$font-2: "font-2" , sans-serif;


*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    list-style: none;
 
   
}
body {
     background: url("../assets/img/Ethnique\ africaine\ motif\ géométrique\ typique.jpeg") center/cover;
     filter:Alpha(opacity=94);-moz-opacity:0.94;opacity: 0.94;
     
}

@mixin flecCenter {
    display: flex;
    justify-content: center;
    align-items: center;
}
a {
    text-decoration: none;
}
a , button {
    cursor: pointer;
}
button {
    transition: 0.2s;

    &:hover {
        transform: scale(1.1);
    }
}

