.form {
    background-color: white;
    height: 120vh;
    // width: 60%;
    margin: 0 auto;
    
    

    .logo {
        
        
            height: 100px;
            padding-top: 60px;
            border-radius: 50%;
            background: white;
            @include flecCenter
        
    }
    form {
        margin-top: 50px;
    display: flex;
    flex-direction: column;
    

    input {
        width: 342px;
        height: 50px;
        border-radius: 10px;
        margin: 15px auto;
        border: 1px solid rgba(0, 0, 0, 0.082);
    }

    #submit {
        width: 342px;
        border-radius: 10px;
        background: $main-color;
        color: $color-2;
        height: 53px;
        font-size: 20px;
        border: none;
        text-align: center;
        transition: 0.2s ease;

        &:hover {
            transform: scale(1.1);
        }
        
        
    }
}

.other-sigup{
    margin-bottom: 50px;
    margin-top: 20px;
    h3 {
        font-size: 20px;
        position: relative;
        text-align: center;
        font-family: $font-1;
        font-weight: bold;
        color: $color-1;

        &::before {
            content: "";
            background: $color-1;
            position: absolute;
            width: 90px;
            height: 1px;
            top: 15px;
            left: 0;
            margin-left: 505px;
        }

        &::after {
            content: "";
            background: $color-1;
            position: absolute;
            width: 90px;
            height: 1px;
            top: 15px;
            right: 0;
            margin-right: 505px;
        }
    }
}

    p {
        text-align: center;
    }
    ul {
        display: flex;
        justify-content: space-around;
        margin: 0 auto;
        width: 350px;
        margin-top: 40px;
        li {
            color: $main-color;
            font-size: 25px;
        }
    }
}