.main {
    background: $color-2;
    width: 70%;
    margin: 0 auto;

    .header {
        display: flex;
        justify-content: space-between;
        width: 80%;
        margin: 0 auto;
        padding-top: 20px;

        .nav {
            display: flex;
            align-items: center;
            font-family: $font-2;
            ul {
                display: flex;
                margin-top: 5px;

                li {
                    padding: 15px;
                    font-size: 20px;
                    transition: 0.2s;
                    cursor: pointer;
                    color: black;

                    &:hover {
                        color: $main-color;
                    }
                    

                    i {
                        margin-right: 5px;
                        font-size: 15px;
                    }
                }
            }
        }
    }
    h1 {
        font-size: 3rem;
        margin-left: 50px;
        margin-top: 40px;
    }

    .search {
        display: flex;
        width: 40%;
        margin: 0 auto;
        margin-top: 40px;
    

        input {
            width: 500px;
            height: 50px;
            border-radius: 20px;
            border: none;

        }

        img {
            background: $main-color;
            width: 60px;
            padding: 10px;
            border-radius: 20px;
            margin-left: 10px;
        }
        
    }

    .title {
      h3 {
        text-align: center;
        font-size: 2.3rem;
        margin-top: 40px;
      }
    }

    .model-container {
        padding: 20px;
        width: 95%;
        margin: 0 auto;
        
        img {
            border: 2px solid $main-color;
            padding: 10px;
            margin: 10px;
            border-radius: 40px;
            height: 286px;
            width: 150px;
        }
    }
    .img-container{
        display: flex;
        width: 95%;
        margin: 0 auto;
        overflow: scroll;
        
        
        img{
            margin: 20px;
            
        }
    }

    .see-all {
        display: flex;
        justify-content: space-between;
        width: 70%;margin: 0 auto;
    }
}