.item-container {
    background: $color-2;
    width: 60%;
    margin: 0 auto;
    //  height: 100vh;
     font-family: $font-2;


    .header {
        display: flex;
        font-family: $font-1;
        padding: 20px 0 40px;
        margin-left: 169px;

        i {
            
            padding: 10px;
            border-radius: 20px;
            color: black;

            &:hover {
                background: $main-color;
                

            }
        }
        h3 {
            margin-left: 35px;
            font-size: 1.4rem;
            margin-top: 5px;
        }
    }

    .img-container {
        display: flex;
        width: 60%;
        margin: 0 auto;
        
        ul{
            background-color: white;
            height: 300px;
            display: flex;
            justify-content: space-around;
            flex-direction: column;
            margin-top: 20px;
            margin-right: 20px;
            width: 50px;
            border-radius: 20px;
            border: 1px solid rgba(128, 128, 128, 0.096);
            
            
            li {
                font-size: 20px;
                margin: 5px;
                transition: 0.3s;
                display: flex;
                align-items: center;
                justify-content: center;
                cursor: pointer;
               
                

                
            }
        }
        img {
            width: 250px;
            height: 350Px;
            border-radius: 15px;
        }
    }

    .description {
        width: 65%;
        margin: 0 auto;
        margin-top: 30px;
        padding-bottom: 50px;

        h3 {
            margin-bottom: 20px;
        }

        .price{
            font-weight: bold;
            margin: 20px 0;
        }

        button {
            background: $main-color;
            border: none;
            color: white;
            padding: 20px;
            font-style: italic;
            width: 250px;
            margin: 0 auto;
            border-radius: 10px;

            i {
                margin-right: 10px;
            }
        }
    }
}

.size-selection {
    background-color: $main-color;
    padding: 10px;
    @include flecCenter;
    border-radius: 50%;

}