.get-start{
    
        width: 70%;
        margin: 0 auto;
        background: $color_1;
        
        color: $color-2;
        font-family: $font-1;
        text-align: center;
        box-shadow: 0 0 5px 0 black;
        border-bottom-right-radius: 10px;
        border-bottom-left-radius: 10px;
        
        
        
    
  
    
    .logo {
        font-family: $font-2;
        padding: 15px 0 35px;
        
        h3 {
            font-size: 24px;
            font-weight: normal;
            font-style: italic;
            

            span{
                color: $main-color;
            }
        }
    }

    h1{
        text-align: left;
        font-weight: 400;
        font-size: 3rem;
        margin-left: 50px;
        line-height: 60px;
        
    }

    .img-container {
        border: 1px solid $main-color;
        border-radius: 268px;
        height: 398px;
        width: 256px;
        margin: 40px auto;
        overflow: hidden;
        @include flecCenter;
        transition: 0.3s ease-in-out;

        &:hover {
            transform: scale(1.1);
        }
        
    }

    button {
        width: 352px;
        height: 75px;
        background-color: #FDAB72;
        border: none;
        border-radius: 22px;
        color: $color-2;
        font-size: 18px;
        font-style: italic;
        margin-top: 50px;
        margin-bottom: 100px;
        opacity: 0;
        transform: translateX(-1000px);
        transition: 0.5s ease-in-out;
        
          
        &:hover {
            box-shadow: 1px 3px 3px 0px black;
        }
         

        i {
            margin-left: 10px;;
        }
    }
}